import Link from 'next/link'
import React from 'react'
import { connect } from 'react-redux'
import {Modal, ModalDialog, Button, Form, Col} from 'react-bootstrap'

class ModalBlock extends React.Component {
    render() {
        var modal = this.props.modal;
        var title = '';
        var body = '';
        var buttons = [];
        
        switch (modal.msg) {
            case 'ASSIGN_TASKS_SUCCESS':
                title = 'הצלחה';
                body = <span>{Object.keys(modal.entries).length}                     האיסופים שובצו בהצלחה! ניתן לראות את כל השיבוצים שלך בעמוד <Link href="assigned-tasks"><a onClick={() => this.props.dispatch({ type: 'CLOSE_MODAL' })}>איסופים קרובים</a></Link>.</span>;
                buttons = [
                    {
                        onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                        color: 'secondary',
                        text: 'סגירה'
                    }];
                break;
            case 'REPORT_FILL':
                title = 'משוב על איסוף שבוצע';
                body =
                    <div>
                        <Form>
                            <Form.Row>
                                <Form.Group as={Col} controlId="formBasicEmail">
                                    <Form.Label>עיקריות</Form.Label>
                                    <Form.Control/>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} controlId="formBasicEmail">
                                    <Form.Label>תוספות</Form.Label>
                                    <Form.Control/>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} controlId="formBasicEmail">
                                    <Form.Label>מאפים</Form.Label>
                                    <Form.Control/>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} controlId="formBasicEmail">
                                    <Form.Label>לחם</Form.Label>
                                    <Form.Control/>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} controlId="formBasicEmail">
                                    <Form.Label>הערות נוספות</Form.Label>
                                    <Form.Control/>
                                </Form.Group>
                            </Form.Row>
                        </Form>
                        <label for="inputGroupSelect01">עיקריות</label>
                        <div class="input-group mb-3">
                            <input type="text" placeholder="כמות" class="form-control" id="basic-url" aria-describedby="basic-addon3" />
                            <div class="input-group-append">
                                <span class="input-group-text" id="basic-addon3">מיכלים</span>
                            </div>
                        </div>
                        <label for="inputGroupSelect01">תוספות</label>
                        <div class="input-group mb-3">
                            <input type="text" placeholder="כמות" class="form-control" id="basic-url" aria-describedby="basic-addon3" />
                            <div class="input-group-append">
                                <span class="input-group-text" id="basic-addon3">מיכלים</span>
                            </div>
                        </div>
                        <label for="inputGroupSelect01">מאפים</label>
                        <div class="input-group mb-3">
                            <input type="text" placeholder="כמות" aria-label="First name" class="form-control" />
                            <select class="custom-select" id="inputGroupSelect03"
                                aria-label="Example select with button addon">
                                <option selected>בחירת סוג</option>
                                <option value="1">ארגזים</option>
                                <option value="2">שקיות סופר</option>
                                <option value="3">שקים</option>
                            </select>
                        </div>
                        <label for="inputGroupSelect01">לחם</label>
                        <div class="input-group mb-3">
                            <input type="text" placeholder="כמות" aria-label="First name" class="form-control" />
                            <select class="custom-select" id="inputGroupSelect03"
                                aria-label="Example select with button addon">
                                <option selected>בחירת סוג</option>
                                <option value="1">ארגזים</option>
                                <option value="2">שקיות סופר</option>
                                <option value="3">שקים</option>
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="exampleFormControlInput1">עמותה</label>
                            <input type="email" class="form-control" id="exampleFormControlInput1" />
                        </div>
                        <div class="form-group">
                            <label for="exampleFormControlTextarea1">הערות נוספות</label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                        </div>
                    </div>;
                buttons = [
                    {
                        onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                        color: 'secondary',
                        text: 'סגירה'
                    }];
                break;
            case 'REPORT_UNDONE':
                title = 'משוב על איסוף שלא בוצע';
                body =
                    <div>
                        <div class="form-group">
                            <label for="exampleFormControlSelect1">מדוע לא בוצע האיסוף?</label>
                            <select class="form-control" id="exampleFormControlSelect1">
                                <option>לא נותר מזון</option>
                                <option>לא נעניתי</option>
                                <option>סיבה אחרת</option>
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="exampleFormControlTextarea1">הערות נוספות</label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                        </div>
                    </div>;
                buttons = [
                    {
                        onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                        color: 'primary',
                        text: 'שליחה'
                    },

                    {
                        onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                        color: 'secondary',
                        text: 'ביטול'
                    }];
                break;
            case 'TASK_NOTES':
                title = 'הערות';
                body = <div>{modal.entries.notes}</div>;
                buttons = [
                    {
                        onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                        color: 'secondary',
                        text: 'סגירה'
                    }];
                break;
            case 'TASK_CANCEL':
                title = 'הסרת שיבוץ';
                body = <div>...</div>;
                buttons = [
                    {
                        onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                        color: 'danger',
                        text: 'הסרה'
                    },
                    {
                        onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                        color: 'secondary',
                        text: 'ביטול'
                    }];
                break;
            case 'EDIT_USER':
                    title = 'עריכת פרטי משתמש';
                    body =
                        <div>
                            <div class="form-group">
                                <label for="exampleFormControlSelect1">שם פרטי</label>
                                <input class="form-control" id="exampleFormControlInput1" value={modal.entries.firstName}/>
                                <label for="exampleFormControlSelect1">שם משפחה</label>
                                <input class="form-control" id="exampleFormControlInput1" value={modal.entries.lastName}/>
                                <label for="exampleFormControlSelect1">דואר אלקטרוני</label>
                                <input type="email" class="form-control" id="exampleFormControlInput1" value={modal.entries.email} />
                                <div className="form-group">
                <label htmlFor="exampleFormControlInput1">כתובת מגורים</label>
                <div className="input-group mb-3">
                    <div className="input-group-prepend">
                        <span className="input-group-text">רחוב</span>
                    </div>
                    <input type="text" className="form-control" aria-label="" />
                </div>
                <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text">מספר</span>
                        </div>
                        <input type="text" className="form-control" aria-label="" />
                </div>
                <div className="input-group mb-3">
                    <div className="input-group-prepend">
                        <span className="input-group-text">עיר</span>
                    </div>
                    <input type="text" className="form-control" aria-label="" />
                </div>
            </div>
                                <label for="exampleFormControlSelect1">אזור שיוך</label>
                                <select class="form-control" id="exampleFormControlSelect1">
                                    <option>ראשון לציון</option>
                                    <option>רעננה</option>
                                    <option>תל אביב</option>
                                </select>
                            </div>
                        </div>;
                    buttons = [
                        {
                            onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                            color: 'primary',
                            text: 'שליחה'
                        },
    
                        {
                            onClick: () => this.props.dispatch({ type: 'CLOSE_MODAL' }),
                            color: 'secondary',
                            text: 'ביטול'
                        }];
                break;
        }

            return (
                <Modal show={modal.isOpen} onHide={() => this.props.dispatch({ type: 'CLOSE_MODAL' })}>
                    <Modal.Header closeButton>
                        <Modal.Title>{title}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        {body}
                    </Modal.Body>
                    <Modal.Footer>
                        {buttons.map((button) => {
                            return (
                                <Button variant={button.color} onClick={button.onClick}>{button.text}</Button>
                            );
                        })}
                    </Modal.Footer>
                </Modal>);
    
        
    }
}

export default connect(state => state)(ModalBlock);